import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Document } from 'src/app/sdk/document/models';
import { HttpClient, HttpEvent, HttpErrorResponse, HttpEventType, HttpResponse } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class SingleUploadService {

    ROOT_SERVER = 'http://erp-document.vm.devetek.com/api';
    constructor(private httpClient: HttpClient) { }

    isreload = new BehaviorSubject<boolean>(false);
    callback = new BehaviorSubject<string>(null);

    param = new BehaviorSubject<Document_file>(null);

    for_module_id = new BehaviorSubject<string>(null);

    getId() {
        return this.for_module_id.asObservable();
    }

    getParam() {
        return this.param.asObservable();
    }
    getisreload() {
        return this.isreload.asObservable();
    }
    getCallback() {
        return this.callback.asObservable();
    }


    public upload(data, userId) {
        let uploadURL = `${this.ROOT_SERVER}/documents`;

        return this.httpClient.post<any>(uploadURL, data, {
            reportProgress: true,
            observe: 'events'
        }).pipe(map((event) => {

            switch (event.type) {

                case HttpEventType.UploadProgress:
                    const progress = Math.round(100 * event.loaded / event.total);
                    return { status: 'progress', message: progress };

                case HttpEventType.Response:
                    return event.body;
                default:
                    return `Unhandled event: ${event.type}`;
            }
        })
        );
    }


}


/* tslint:disable */
export interface Document_file {

    /**
     * for_module
     */
    for_module?: string;

    /**
     * for_table
     */
    for_table?: string;

    /**
     * for_module_id
     */
    for_module_id?: string;

    /**
     * name
     */
    name?: string;

    /**
     * revisi
     */
    revisi?: string;

    /**
     * nomor
     */
    nomor?: string;

}

