import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzMessageService, UploadFile } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';

import { SingleUploadService, Document_file } from '../single-upload.service';
import { FileDocumentService } from 'src/app/sdk/document/services/file-document.service';

@Component({
	selector: 'erp-single-upload',
	templateUrl: './single-upload.component.html',
	styleUrls: ['./single-upload.component.scss']
})
export class SingleUploadComponent implements OnInit {

	constructor(
		private spinner: NgxSpinnerService,
		private notification: NzNotificationService,
		private pesan: PesanService,
		private router: Router,
		private fileDocumentService: FileDocumentService,
		private singleUploadService: SingleUploadService,
		private msg: NzMessageService
	) { }

	param: Document_file = null;

	ngOnInit() {
		this.singleUploadService.getParam().subscribe(
			data => {
				this.param = data;

			}
		)

	}

	upload() {
		this.spinner.show()
		this.fileDocumentService.postFileDocuments(this.param).subscribe(
			data => {
				this.spinner.hide()
				this.singleUploadService.isreload.next(true)
			}
		)
	}

	uploading = false;
	fileList: UploadFile[] = [];
	name = '';
	revisi = '';
	nomor = '';
	beforeUpload = (file: UploadFile): boolean => {
		this.fileList = this.fileList.concat(file);
		return false;
	};

	handleUpload(): void {
		const formData = new FormData();
		// tslint:disable-next-line:no-any
		this.fileList.forEach((file: any) => {
			console.log(file)
			formData.append('files', file);
			formData.append('name', this.name);
			formData.append('revisi', this.revisi);
			formData.append('nomor', this.nomor);
			formData.append('for_module', this.param.for_module);
			formData.append('for_table', this.param.for_table);
			formData.append('for_module_id', this.param.for_module_id);
		});
		this.uploading = true;
		let datas: any = formData;

		this.fileDocumentService.postFileDocuments(datas).subscribe(
			data => {
				this.spinner.hide()
				this.uploading = false;
				let for_module_id: any = data.data;
				this.singleUploadService.for_module_id.next(for_module_id);
				this.singleUploadService.callback.next(this.param.for_module + '.finish');
			},
			rr => {
				this.uploading = false;
			}
		)

	}



}
