import { BrowserModule } from '@angular/platform-browser'
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'
import { NgModule, LOCALE_ID } from '@angular/core'
import { FormsModule, ReactiveFormsModule } from '@angular/forms'
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http'
import { IconDefinition } from '@ant-design/icons-angular'
import * as AllIcons from '@ant-design/icons-angular/icons'
import { NZ_ICONS, NgZorroAntdModule, NzTableModule, NZ_DATE_LOCALE, id_ID } from 'ng-zorro-antd'
import { NgProgressModule } from '@ngx-progressbar/core'
import { NgProgressRouterModule } from '@ngx-progressbar/router'
import { NgProgressHttpModule } from '@ngx-progressbar/http'
import { AngularFireModule } from '@angular/fire'
import { AngularFireAuthModule } from '@angular/fire/auth'
import { AngularFirestoreModule, FirestoreSettingsToken } from '@angular/fire/firestore'

import { AppRoutingModule } from './app-routing.module'
import { AppComponent } from './app.component'
import { StoreModule } from '@ngrx/store'
import { StoreRouterConnectingModule } from '@ngrx/router-store'
import { reducers, metaReducers } from './store/reducers'
import { NgxSpinnerModule } from "ngx-spinner";
/**
 * Locale Registration
 */
import { registerLocaleData } from '@angular/common'
import { default as localeEn } from '@angular/common/locales/en'
import { NZ_I18N, en_US as localeZorro } from 'ng-zorro-antd'
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from './components/widgets/widgets-components.module';
import { VendorModule } from './pages/vendor/vendor.module';
import { ApiModule } from './sdk/user/api.module'
import { JwtInterceptor } from './_role/jwt.interceptor'
import { ErrorInterceptor } from './_role/error.interceptor'


const LOCALE_PROVIDERS = [
    { provide: LOCALE_ID, useValue: 'en' },
    { provide: NZ_I18N, useValue: localeZorro }
]
registerLocaleData(localeEn, 'en')

/**
 * AntDesign Icons
 */
const antDesignIcons = AllIcons as {
    [key: string]: IconDefinition
}
const icons: IconDefinition[] = Object.keys(antDesignIcons).map(key => antDesignIcons[key])

/**
 * Firebase Config
 */
const firebaseConfig = {
    apiKey: 'AIzaSyA2LHKgdr2GQb_QUBYfhMOaxgOuGjw1z5E',
    authDomain: 'airui-a4b63.firebaseapp.com',
    databaseURL: 'https://airui-a4b63.firebaseio.com',
    projectId: 'airui-a4b63',
    storageBucket: 'airui-a4b63.appspot.com',
    messagingSenderId: '1039460737420',
}

@NgModule({
    declarations: [AppComponent],
    imports: [
        HttpClientModule,
        BrowserModule,
        BrowserAnimationsModule,
        FormsModule,
        HttpClientModule,
        /**
         * NgRx Store
         */
        StoreModule.forRoot(reducers, { metaReducers }),
        StoreRouterConnectingModule.forRoot(),

        /**
         * Nprogress Modules
         */
        NgProgressModule.withConfig({
            thick: true,
            spinner: false,
            color: '#0190fe',
        }),
        NgProgressRouterModule,
        NgProgressHttpModule,

        /**
         * Firebase Modules
         */
        AngularFireModule.initializeApp(firebaseConfig),
        AngularFireAuthModule,
        AngularFirestoreModule,

        /**
         * Routing Module
         */
        AppRoutingModule,


        PerfectScrollbarModule,
        WidgetsComponentsModule,
        ReactiveFormsModule,

        NgxSpinnerModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-user.vm.devetek.com/api' }),
        VendorModule,
    ],
    providers: [
        ...LOCALE_PROVIDERS,
        { provide: NZ_ICONS, useValue: icons },
        // { provide: FirestoreSettingsToken, useValue: {} },
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    ],
    bootstrap: [AppComponent],
})
export class AppModule { }
