import { FileDocumentService } from 'src/app/sdk/document/services/file-document.service';
import { Component, OnInit, Input } from '@angular/core';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyLisensiService, CompanyPerusahaanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';

import { HttpClient } from '@angular/common/http';
import { saveAs } from 'file-saver';

@Component({
    selector: 'erp-download-mode1',
    templateUrl: './download-mode1.component.html',
    styleUrls: ['./download-mode1.component.scss']
})
export class DownloadMode1Component implements OnInit {

    constructor(
        private pesan: PesanService,
        private fileDocumentService: FileDocumentService,
        private spinner: NgxSpinnerService,
        private httpClient: HttpClient
    ) { }
    @Input() formoduleid: string;
    @Input() type: string;

    ngOnInit() {

    }

    download() {
        console.log(this.formoduleid);
        this.spinner.show()
        this.fileDocumentService.getFileDocumentsId(this.formoduleid).subscribe(
            datas => {
                this.spinner.hide()

                //window.location.href = 'http://erp-document.vm.devetek.com/download?id_document=' + this.formoduleid;
                this.download_file().subscribe(data => saveAs(data, datas.data.nama_file));
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('file tidak di temukan');
            }
        )
    }

    download_file() {
        return this.httpClient.get('http://erp-document.vm.devetek.com/download?id_document=' + this.formoduleid,
            { responseType: 'blob' });
    }

}
