import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SingleUploadComponent } from './single-upload/single-upload.component';
import { ApiModule } from 'src/app/sdk/document/api.module';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from '../widgets/widgets-components.module';
import { NzLayoutModule, NzPopconfirmModule, NgZorroAntdModule } from 'ng-zorro-antd';
import { LayoutModule } from '../layout/layout.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { DownloadMode1Component } from './download-mode1/download-mode1.component';
import { RiwayatComponent } from './riwayat/riwayat.component';


const COMPONEN = [
    SingleUploadComponent,
    DownloadMode1Component,
    RiwayatComponent
]

@NgModule({
    declarations: [...COMPONEN],
    imports: [
        CommonModule, CommonModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-document.vm.devetek.com/api' }),
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        NzPopconfirmModule,
        LayoutModule,
        ReactiveFormsModule,
        FormsModule,
        NgZorroAntdModule,
    ],
    exports: [...COMPONEN],
    entryComponents: [...COMPONEN]
})
export class DocumentModule { }
