import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VendorComponent } from './vendor.component';
import { AuditedComponent } from './audited/audited.component';
import { UnauditedComponent } from './unaudited/unaudited.component';


const routes: Routes = [
    {
        path: '',
        component: VendorComponent,
        children: [
            {
                path: '',
                redirectTo: 'vendor',
                pathMatch: 'full'
            },
            {
                path: 'audited',
                component: AuditedComponent
            },
            {
                path: 'unaudited',
                component: UnauditedComponent
            },
            {
                path: 'vendor',
                loadChildren: () => import('./vendor/vendor.module').then(mod => mod.VendorModule),
            },
            {
                path: 'kategori-master',
                loadChildren: () => import('./master/kategori-master/kategori-master.module').then(mod => mod.KategoriMasterModule),
            },
            {
                path: 'manufacture',
                loadChildren: () => import('./manufacture/manufacture.module').then(mod => mod.ManufactureModule),
            },
            {
                path: 'contact',
                loadChildren: () => import('./contact/contact.module').then(mod => mod.ContactModule),
            },
            {
                path: 'audit',
                loadChildren: () => import('./audit/audit.module').then(mod => mod.AuditModule),
            },
            {
                path: 'detail-vendor/:id_vendor',
                loadChildren: () => import('./detail-vendor/detail-vendor.module').then(mod => mod.DetailVendorModule),
            },
            {
                path: 'detail-manufacture/:id_manufacture',
                loadChildren: () => import('./detail-manufacture/detail-manufacture.module').then(mod => mod.DetailManufactureModule),
            },
        ]
    }
];


@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class VendorRoutingModule { }
