import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'statusAsset'
})
export class StatusAssetPipe implements PipeTransform {

    transform(value: any): any {
        let warna = '#fff';
        if (value == 'pending') {
            warna = 'green'
        }
        return warna;

    }

}
