import { Injectable } from '@angular/core'
import { Router } from '@angular/router'
import { NzNotificationService } from 'ng-zorro-antd'
import { AuthAuthJwtService } from '../sdk/user/services'
import { UserInfoService } from './user-info.service'

interface User {
    uid: string
    email: string
    displayName: string
    photoURL: string
    emailVerified: boolean
    role: string
}

@Injectable({
    providedIn: 'root',
})
export class AuthService {
    userData: any

    constructor(
        // public afAuth: AngularFireAuth,
        public afAuth: AuthAuthJwtService,
        public router: Router,
        private notification: NzNotificationService,
        private userService: UserInfoService
    ) {

    }

    async SignIn(email: string, password: string) {
        try {
            this.afAuth.postAuthAuthJwts({ email: email, password: password })
                .subscribe(
                    data => {
                        this.router.navigate(['dashboard/analytics'])
                        this.notification.success(
                            'Logged In',
                            'You have successfully logged in to Air UI Angular Admin Template!',
                        )

                        if (data) {
                            this.userData = data
                            localStorage.setItem('user', JSON.stringify(this.userData))
                            this.userService.callToGetUser.next(true)

                        } else {
                            localStorage.setItem('user', null)
                        }
                    },
                    error => {
                        this.notification.error(error.code, error.message)
                    }
                )

        } catch (error) {
            this.notification.error(error.code, error.message)
        }
    }

    get isLoggedIn(): boolean {
        const user = JSON.parse(localStorage.getItem('user'))
        return user !== null
    }

    get getUser(): { token: string } {
        const user = JSON.parse(localStorage.getItem('user'))
        if (user) {
            return user;
        }
        return null;
    }

    async SignOut() {
        // await this.afAuth.auth.signOut()
        this.afAuth.deleteAuthAuthJwtsId('1').subscribe(
            data => {
                localStorage.removeItem('user')
                this.router.navigate(['system/login'])
            },
            err => {
                localStorage.removeItem('user')
                this.router.navigate(['system/login'])
            }
        )

    }

    getExpiration() {
        const expiration = localStorage.getItem("expires_at");
        const expiresAt = JSON.parse(expiration);
        // return moment(expiresAt);
    }
}
