import { Injectable } from '@angular/core'
import { Observable, of, BehaviorSubject } from 'rxjs'

@Injectable({
    providedIn: 'root',
})
export class MenuService {
    constructor() { }

    menu = new BehaviorSubject<any>([])

    subBar_info = new BehaviorSubject<{ parent: string, child: string }>({ parent: "App", child: '' })
    subBar_shorcut = new BehaviorSubject<[]>([])
    getMenuData(): Observable<any[]> {
        return this.menu.asObservable()
    }

    getSubBarInfo(): Observable<{ parent: string, child: string }> {
        return this.subBar_info.asObservable()
    }

    getShorcut(): Observable<[]> {
        return this.subBar_shorcut.asObservable()
    }
}
