/* tslint:disable */
import { NgModule, ModuleWithProviders } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationInterface } from './api-configuration';

import { AuthAuthJwtService } from './services/auth-auth-jwt.service';
import { FileDokumenService } from './services/file-dokumen.service';
import { PurchasingPurchaseOrderService } from './services/purchasing-purchase-order.service';
import { PurchasingPurchaseOrderItemService } from './services/purchasing-purchase-order-item.service';
import { PurchasingPurchaseRequestService } from './services/purchasing-purchase-request.service';
import { PurchasingPurchaseRequestItemService } from './services/purchasing-purchase-request-item.service';
import { ToolsDataFlowService } from './services/tools-data-flow.service';
import { ToolsDataFlowDetailService } from './services/tools-data-flow-detail.service';
import { ToolsLogFlowService } from './services/tools-log-flow.service';
import { ToolsServiceDataFlowService } from './services/tools-service-data-flow.service';
import { UserGroupService } from './services/user-group.service';
import { UserGroupDetailService } from './services/user-group-detail.service';
import { UserModuleService } from './services/user-module.service';
import { UserModuleAppService } from './services/user-module-app.service';
import { UserModuleDetailService } from './services/user-module-detail.service';
import { UserUsersService } from './services/user-users.service';
import { UserPegawaiLinkUserPegawaiLinkService } from './services/user-pegawai-link-user-pegawai-link.service';
import { VendorContactService } from './services/vendor-contact.service';
import { VendorManufactureService } from './services/vendor-manufacture.service';
import { VendorVendorService } from './services/vendor-vendor.service';

/**
 * Provider for all Api services, plus ApiConfiguration
 */
@NgModule({
  imports: [
    HttpClientModule
  ],
  exports: [
    HttpClientModule
  ],
  declarations: [],
  providers: [
    ApiConfiguration,
    AuthAuthJwtService,
    FileDokumenService,
    PurchasingPurchaseOrderService,
    PurchasingPurchaseOrderItemService,
    PurchasingPurchaseRequestService,
    PurchasingPurchaseRequestItemService,
    ToolsDataFlowService,
    ToolsDataFlowDetailService,
    ToolsLogFlowService,
    ToolsServiceDataFlowService,
    UserGroupService,
    UserGroupDetailService,
    UserModuleService,
    UserModuleAppService,
    UserModuleDetailService,
    UserUsersService,
    UserPegawaiLinkUserPegawaiLinkService,
    VendorContactService,
    VendorManufactureService,
    VendorVendorService
  ],
})
export class ApiModule {
  static forRoot(customParams: ApiConfigurationInterface): ModuleWithProviders {
    return {
      ngModule: ApiModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: {rootUrl: customParams.rootUrl}
        }
      ]
    }
  }
}
