import { Component, OnInit } from '@angular/core';
import { SingleUploadService, Document_file } from '../single-upload.service';
import { NzMessageService } from 'ng-zorro-antd';
import { FileDocumentService } from 'src/app/sdk/document/services';
import { Document } from 'src/app/sdk/document/models';

@Component({
    selector: 'erp-riwayat-upload',
    templateUrl: './riwayat.component.html',
    styleUrls: ['./riwayat.component.scss']
})
export class RiwayatComponent implements OnInit {

    constructor(
        private singleUploadService: SingleUploadService,
        private msg: NzMessageService,
        private fileDocumentService: FileDocumentService
    ) { }

    param: Document_file = null;

    ngOnInit() {
        this.singleUploadService.getParam().subscribe(
            data => {
                this.param = data;
                this.getData()
            }
        )
    }

    is_loading = true
    listData: Document[] = []
    getData() {
        this.is_loading = true
        this.fileDocumentService.getFileDocuments(JSON.stringify(this.param)).subscribe(
            data => {
                this.listData = data.data
                this.is_loading = false
            }
        )

    }


}
