import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'status'
})
export class StatusPipe implements PipeTransform {

    transform(value: any, ...args: any[]): any {
        if (value == 'Draft') {
            return '#A6ACAF'
        }
        if (value == 'Need Approval') {
            return '#FFC300'
        }
        if (value == 'Open') {
            return '#87d068'
        }
        if (value == 'Closed') {
            return '#108ee9'
        }
        if (value == 'Reject') {
            return '#f50'
        }
        if (value == 'acc') {
            return 'red'
        }


        return '#A569BD';
    }

}
