import { Component, OnInit } from '@angular/core'
import { MenuService } from 'src/app/services/menu.service';

@Component({
    selector: 'air-subbar',
    templateUrl: './subbar.component.html',
    styleUrls: ['./subbar.component.scss'],
})
export class SubbarComponent implements OnInit {
    constructor(
        private menuService: MenuService
    ) { }

    shortcut = [];
    menuInfo: { parent: string, child: string } = { parent: 'App', child: '' }
    ngOnInit() {
        this.menuService.getSubBarInfo().subscribe(
            data => {
                this.menuInfo = data;
            }
        )
        this.menuService.getShorcut().subscribe(
            data => {
                this.shortcut = data;
            }
        )
    }
}
